import React, { Component } from 'react';
import { connect } from 'react-redux';

import { signOut } from 'actions/auth';

class Signin extends Component {
  componentWillMount() {
    const { signOut } = this.props;

    signOut();
  }
  render() {
    return (
      <div>Signed out.</div>
    );
  }
}

module.exports = connect(null, { signOut })(Signin);



